<template>
  <div id="foo">
    <ul>
      <li v-for="article in articles">
        {{article.title}}
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: 'Foo',
    data () {
      return {
        articles: []
      }
    },
    mounted: function() {
      this.$http.jsonp('https://api.douban.com/v2/movie/top250?count=10', {}, {
        headers: {

        },
        emulateJSON: true
      }).then(function(response) {
        // 
        console.log(response)
		alert(response)
        debugger
        this.articles = response.data.subjects
        // this.articles = response.data["subjects"] 

      }, function(response) {
        // 
        console.log(response)
      });
    }

  }
</script>

<style>
</style>



